import React, { useEffect, useRef } from 'react';
import EditorJS from '@editorjs/editorjs';
import Header from '@editorjs/header';
import List from '@editorjs/list';
import Checklist from '@editorjs/checklist';
import Quote from '@editorjs/quote';
import Warning from '@editorjs/warning';
import Marker from '@editorjs/marker';
import Code from '@editorjs/code';
import Delimiter from '@editorjs/delimiter';
import InlineCode from '@editorjs/inline-code';
import LinkTool from '@editorjs/link';
import Embed from '@editorjs/embed';
import Table from '@editorjs/table';
import SimpleImage from '@editorjs/simple-image';

import './editorJS.css';

const EditorJSComponent = () => {
  const editorInstance = useRef(null);

  useEffect(() => {
    // Inicializa o editor com as ferramentas e dados padro
    editorInstance.current = new EditorJS({
      holder: 'editorjs',
      tools: {
        header: { class: Header, inlineToolbar: ['marker', 'link'], config: { placeholder: 'Header' }, shortcut: 'CMD+SHIFT+H' },
        image: SimpleImage,
        list: { class: List, inlineToolbar: true, shortcut: 'CMD+SHIFT+L' },
        checklist: { class: Checklist, inlineToolbar: true },
        quote: { class: Quote, inlineToolbar: true, config: { quotePlaceholder: 'Enter a quote', captionPlaceholder: "Quote's author" }, shortcut: 'CMD+SHIFT+O' },
        warning: Warning,
        marker: { class: Marker, shortcut: 'CMD+SHIFT+M' },
        code: { class: Code, shortcut: 'CMD+SHIFT+C' },
        delimiter: Delimiter,
        inlineCode: { class: InlineCode, shortcut: 'CMD+SHIFT+C' },
        linkTool: LinkTool,
        embed: Embed,
        table: { class: Table, inlineToolbar: true, shortcut: 'CMD+ALT+T' }
      },
      data: null,
      onReady: () => {
        console.log('Editor.js is ready to work!');
      },
    });

    // Funo para salvar dados, exposta globalmente
    window.handleSave = () => {
      editorInstance.current
        .save()
        .then((outputData) => {
          if (window.chrome && window.chrome.webview) {
            window.chrome.webview.postMessage(JSON.stringify(outputData));
          }
        })
        .catch((error) => {
          console.log('Erro ao salvar:', error);
        });
    };

    // Funo para limpar o contedo do editor, exposta globalmente
    window.handleClear = () => {
      editorInstance.current
        .clear()
        .then(() => {
          console.log('Editor cleared');
        })
        .catch((error) => {
          console.log('Erro ao limpar:', error);
        });
    };

    // Funo para carregar dados JSON no editor, exposta globalmente
    window.handleLoad = (jsonData) => {
      try {
        const data = JSON.parse(jsonData); // Converte a string JSON para um objeto JavaScript
        editorInstance.current
          .render(data) // Carrega os dados no editor
          .then(() => {
            console.log('Editor carregado com novos dados');
          })
          .catch((error) => {
            console.log('Erro ao carregar dados no editor:', error);
          });
      } catch (error) {
        console.log('Erro ao analisar JSON:', error);
      }
    };

    // Limpeza do editor ao desmontar o componente
    return () => {
      editorInstance.current.isReady
        .then(() => {
          editorInstance.current.destroy();
        })
        .catch((e) => console.log('Editor.js was not ready', e));
    };
  }, []);

  return (
    <div className="mte-block">
      <div id="editorjs"></div>
    </div>
  );
};

export default EditorJSComponent;